import Vue from 'vue'
import VueRouter from 'vue-router'
import axios from 'axios';
import Home from '../views/Home.vue'
import Login from '../views/Login.vue'
import User_Home from '../views/Users/Home.vue'
import PageNotFound from '../views/PageNotFound.vue'
import SubsYoutube from '../components/SubsYoutube.vue'
import MessageRandom from '../components/MessageRandom.vue'


const MODE = process.env.NODE_ENV || "development";
const URL = MODE == "development" ? "http://localhost:3000/api/v1" : "https://asdiegoya.azurewebsites.net/api/v1";


Vue.use(VueRouter)

async function existUser(token) {
  var api = URL + "/auth/home";
  var response = await axios.get(api, {
    headers: {
      "x-access-token": token,
    },
  });

  var auth = false;
  if (response) {
    var data = response.data;
    auth = data.auth;
  }
  return auth;
}

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/login',
    name: 'Login',
    component: Login,
    beforeEnter: async (to, from, next) => {
      var token = window.localStorage.getItem("token-asdiegoYA");
      if (token == null) {
        next();
        return;
      }

      var auth = await existUser(token);

      //Si todo esta correcto
      if (auth) {
        next("home");
      } else {
        next();
      }
    }
  },
  {
    path: '/subsYoutube',
    name: 'SubsYoutube',
    component: SubsYoutube
  },
  {
    path: '/messageRandom',
    name: 'MessageRandom',
    component: MessageRandom,
  },
  {
    path: '/home',
    name: 'User_Home',
    component: User_Home,
    beforeEnter: async (to, from, next) => {
      var token = window.localStorage.getItem("token-asdiegoYA");
      if (token == null) {
        next("login");
        return;
      }

      var auth = await existUser(token);

      //Si todo esta correcto
      if (auth) {
        next();
      } else {
        next("login");
      }
    }
  },
  {
    path: '/servers',
    name: 'Servers',
    // route level code-splitting
    // this generates a separate chunk (Servers.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "Servers" */ '../views/Servers.vue')
  },
  { path: "*", component: PageNotFound }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
